/**
 * Action 类似于 mutation
 * Action 可以包含任意异步操作。
 */
export default {
    /**
     * 获取 openid 的方法
     */
    getOpenid: ({commit}, param) => {
        return new Promise((resolve, reject) => {
            fetch(`http://picc.hotgz.com/GetOpenID.aspx?code=${param}`, {
                'method': 'GET',
                'contentType': "application/json; charset=utf-8",
            }).then(
                response => response.text(),
                error => error
            ).then(openid => {
                commit('initOpenid', {openid: openid});
                resolve(openid);
            }).catch(error => {
                reject('向服务器获取openid信息发生错误!');
            });
        });
    },
}



