let config = (() => {
    let NODE_ENV = process.env.NODE_ENV || '';

    if (NODE_ENV === 'development' || window.location.href.includes('.demo.hotgz.com')) {
        return {
            basicUrl: 'http://api.demo.hotgz.com'
        }
    } else {
        return {
            basicUrl: 'http://ycpdapi.hotgz.com'
        }
    }
})();

export default config



