/**
 * Action 类似于 mutation
 * Action 可以包含任意异步操作。
 */
import cookies from './../../utils/cookies';
import config from './../../config';

export default {
    /**
     * 获取 用户信息的方法
     * @param {string} param openid
     */
    verifyLogIn: ({commit}, param) => {
        return new Promise((resolve, reject) => { // 返回一个 promise
            fetch(`http://ycpdapi.hotgz.com/Customer/GetCustomerInfoByOpenID?OpenID=${param}`, {
                'method': 'POST',
                'contentType': "application/json; charset=utf-8"
            }).then(
                response => response.json(),
                error => error
            ).then(val => {
                if (val.Code === 200) { // 获取成功
                    commit('initUserInfo', {userInfo: val.Data}); // 初始化 登录信息 传入到 mutations 里面
                    resolve(val.Data);
                }
                reject(`向服务器获取用户信息成功, 但是数据有误. 原因:${JSON.stringify(val)}`);
            }).catch(error => {
                reject('向服务器获取用户信息发生错误!');
            });
        })
    },
}



